import {
  Component,
  ChangeDetectionStrategy,
  ViewChild,
  TemplateRef,
  OnInit
} from '@angular/core';
import {
  startOfDay,
  endOfDay,
  subDays,
  addDays,
  endOfMonth,
  isSameDay,
  isSameMonth,
  addHours
} from 'date-fns';
import { Subject } from 'rxjs/Subject';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ActivatedRoute } from '@angular/router';
import * as $ from 'jquery';
import {
  CalendarEvent,
  CalendarEventAction,
  CalendarEventTimesChangedEvent
} from 'angular-calendar';

import { ActivityService } from '../service/activityService';

const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  }
};

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'app-complience-calendar',
  templateUrl: './complience-calendar.component.html',
  styleUrls: ['./complience-calendar.component.css'],
  providers: [ActivityService]
})
export class ComplienceCalendarComponent implements OnInit {
  @ViewChild('modalContent') modalContent: TemplateRef<any>;

  view: string = 'month';

  viewDate: Date = new Date();

  modalData: {
    action: string;
    event: CalendarEvent;
  };

  actions: CalendarEventAction[] = [
    {
      label: '<i class="fa fa-fw fa-pencil"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.handleEvent('Edited', event);
      }
    },
    {
      label: '<i class="fa fa-fw fa-times"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.events = this.events.filter(iEvent => iEvent !== event);
        this.handleEvent('Deleted', event);
      }
    }
  ];

  refresh: Subject<any> = new Subject();

  events: CalendarEvent[] = [
    // {
    //   start: subDays(startOfDay(new Date("2018-04-10")), 0),
    //   end: addDays(new Date("2018-04-10"), 0),
    //   title: 'Monthly outward supplies return',
    //   color: colors.blue,
    // //  actions: this.actions
    // },
    // {
    //   start: subDays(startOfDay(new Date("2018-04-30")), 0),
    //   end: addDays(new Date("2018-04-30"), 0),
    //   title: 'TDS on Salary Payment',
    //   color: colors.blue,
    // //  actions: this.actions
    // },
    // {
    //   start: subDays(startOfDay(new Date("2018-04-30")), 0),
    //   end: addDays(new Date("2018-04-30"), 0),
    //   title: 'Payment of TDS on overseas Remittance',
    //   color: colors.blue,
    // //  actions: this.actions
    // },
    // {
    //   start: subDays(endOfMonth(new Date("2018-04-30")), 0),
    //   end: addDays(endOfMonth(new Date("2018-04-30")), 0),
    //   title: 'TDS on Salary Payment',
    //   color: colors.blue
    // },
    // {
    //   start: addHours(startOfDay(new Date("2018-04-30")), 1),
    //   end: new Date("2018-04-30"),
    //   title: 'Payment of TDS on overseas Remittance',
    //   color: colors.blue,
    //  // actions: this.actions,
    //   resizable: {
    //     beforeStart: true,
    //     afterEnd: true
    //   },
    //   draggable: true
    // }
  ];

  activeDayIsOpen: boolean = false;

  constructor(private modal: NgbModal, private activityService: ActivityService, private route: ActivatedRoute) {}

   public activityData:any = [];
   activitySeverity: string;
   lawArea: string;
  private sub: any = [];

   private activityDataOriginal:any = [];
   public activityDataOnlyCalander:any = [];
   private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  buildActivityCalander(response):any{
     this.activityData = response;
     this.activityDataOriginal = this.activityData;
    for (let i=0; i< response.length ; i++){
      let activityCalanderObj = {};
      activityCalanderObj['start'] = addHours(startOfDay(new Date(response[i]['duedate'])), 0);
      activityCalanderObj['end'] = new Date(response[i]['duedate']);
      activityCalanderObj['title'] = response[i]['activityName'];
      activityCalanderObj['color'] = colors.blue;
      activityCalanderObj['activity'] = response[i];
      this.activityDataOnlyCalander.push(activityCalanderObj);
    }
     this.events = this.activityDataOnlyCalander;
     this.refresh.next();
     if(this.activitySeverity){
        this.view = 'day';
        this.activityData = this.activityDataOriginal;
     }
  }

  ngOnInit() {
   let companies = sessionStorage.getItem('companies');
    let data = JSON.parse(companies);
    if(this.route.queryParams) {
    this.sub = this.route.queryParams.subscribe(params => {
       this.activitySeverity = params['activitySeverity'];
       this.lawArea = params['lawArea'];
       console.log(this.activitySeverity);
    });
  }
    this.activityService.getActivityData(this.activitySeverity,this.lawArea).then(response => {this.buildActivityCalander(response);})
      .catch(err=>{this.handleError(err);});

  }

  dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
    if (isSameMonth(date, this.viewDate)) {
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
        this.viewDate = date;
      }
    }
  }

  eventTimesChanged({
    event,
    newStart,
    newEnd
  }: CalendarEventTimesChangedEvent): void {
    event.start = newStart;
    event.end = newEnd;
    this.handleEvent('Dropped or resized', event);
    this.refresh.next();
  }

  handleEvent(action: string, event: CalendarEvent): void {
    let activityArray:Array<Object> = [];
    let activity:Object = event['activity'];
    activityArray.push(activity);
    this.activityData = activityArray;
    this.view = 'day';
    // this.modalData = { event, action };
    // this.modal.open(this.modalContent, { size: 'lg' });
  }

  addEvent(): void {
    this.events.push({
      title: 'New event',
      start: startOfDay(new Date()),
      end: endOfDay(new Date()),
      color: colors.red,
      draggable: true,
      resizable: {
        beforeStart: true,
        afterEnd: true
      }
    });
    this.refresh.next();
  }
}


