import { Component,OnInit } from '@angular/core';
import { LoginService } from '../service/loginService';
import { LoginBean } from '../typeBean/loginBean';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'Login-Page',
  templateUrl: './app.login.login.html',
  styleUrls: ['../app.component.css'],
  providers: [LoginService]
})
export class LoginLoginComponent implements OnInit{
	constructor(private loginService: LoginService,private route: ActivatedRoute) { }
	
	responseStr:any = {};
	username = "";
	password = "";
	hasError = false;
	loginBean :LoginBean = new LoginBean();
	userNotActive = false;
	loginInProgress = "enabled";
	
	private handleError(error: any): Promise<any> {
	  console.error('An error occurred', error); // for demo purposes only
	  return Promise.reject(error.message || error);
	}
	
	login(): void {
		this.loginInProgress = "disabled";
	  this.loginService.loginUserPost(this.loginBean.username,this.loginBean.password).then(responseStr => {
		  this.loginInProgress = "enabled";
		  this.responseStr = responseStr;
				if(this.responseStr.isUserActive){
					const redirectionUrl: string = this.route.snapshot.queryParamMap.get('redirectionUrl');
					if(redirectionUrl){
						window.location.hash=redirectionUrl;
					}else{
						window.location.href="#/dashboard"
					}
					
				}else{
					//window.location.href="#/notactive"
					//User is not active show sendActivation email
					this.userNotActive = true;
				}				
		  })
			.catch(err=>{
				this.handleError(err);
				this.hasError = true;
				this.loginInProgress = "enabled";});
	}
	
	hideError():void{
		this.hasError = false;
	}
	
	reSendRegistrationCode(){
		console.log("Send registation code....");
		this.loginService.reSendUserActivationCode();
	}
	
	ngOnInit(): void {
		var that = this;
	}
	  
}



