import { Component,OnInit,HostListener  } from '@angular/core';
import { LoginService } from './service/loginService';
import { LocationService } from './service/locationService';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css', '../../node_modules/angular-calendar/css/angular-calendar.css'],
  providers: [LoginService,LocationService]
}) 
export class AppComponent implements OnInit{
	constructor(private loginService: LoginService) { }
  
	title = 'app';
	userData = {};
	hasError = false;
	
	loggedIn = this.loginService.isLoggedIn()?true:false;
  
	handleError(err:any):void{
		//window.location.href="/"
	}
	
	@HostListener('window:hashchange', ['$event'])
		hashChangeHandler(e) {
		if(window.location.hash == '' || window.location.hash == "#/"){
			this.loggedIn = false;
			this.loginService.clearUserLogin();
		}else{
			this.loggedIn = this.loginService.isLoggedIn()?true:false;
			this.getLoggedInUser();	
		}
	}

	getLoggedInUser():void{
		this.loginService.loggedInUser().then(userDatan => {
		  console.log(userDatan);
		  this.userData = userDatan;
		}).catch(err=>{this.handleError(err);this.hasError = true;});
	}
	
	ngOnInit(): void {
		var that = this;		
	}
}



