import { Component, OnInit } from '@angular/core';
import {LocationService} from '../service/locationService';
import {LoginService} from '../service/loginService';
import { ActivatedRoute, Router } from '@angular/router';


@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {

  constructor(private locationService: LocationService,private loginService:LoginService,private router: Router, private route: ActivatedRoute) {}
  
  companyLocations;
  currentSelectedLocation;
  userType;
  public company_json:any = {};
  public allCompanyIds:string = "";

   private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  private setLocation(location:string){
    this.loginService.setLocation(location);
  }

  public onLocationChange(locationId:string){
    console.log("sudheer");
    console.log(locationId);
    this.loginService.setLocation(locationId);
    //location.href="#/dashboard";
console.log(this.router.url);
let prevroute = this.router.url;
//this.router.navigateByUrl('/RefrshComponent', {skipLocationChange: true}).then(()=>
// this.router.navigate([somewhere else]..)
//     .then(()=>{this.router.navigate(..back..)})

this.router.navigateByUrl('blank').then(() => {
          this.router.navigateByUrl(prevroute);
          console.log('naviate to any route which you want');
    });

//this.router.navigate([this.router.url], { queryParams: {location : locationId} });
    //this.router.navigateByUrl(this.router.url, {skipLocationChange: false})
  }

  public setLoadUserTypeProfile(){
    this.userType = this.loginService.getUserRole();
  }


public setCompany(company:string) {
   this.loginService.setCompany(company);
}

  public onCompanyChange(companyId: string) {
   this.loginService.setCompany(companyId);

   //On companychange reload location and set location to ALL
   this.locationService.getAllLocations().then(response => {
    console.log(response);
    this.companyLocations = response;      
    this.setLocation("all");
  })
  .catch(err=>{this.handleError(err);});	
  
   let prevroute = this.router.url;
   this.router.navigateByUrl('blank').then(() => {
          this.router.navigateByUrl(prevroute);
          console.log('naviate to any route which you want');
    });

  }


  ngOnInit() {
    let companies = sessionStorage.getItem('companies');
    this.company_json = JSON.parse(companies);
    let allCompanyIdsArr = [];
    //Adding all company locations as coma seperated values
    this.company_json.forEach((data)=>{
      allCompanyIdsArr.push(data.companyId);
    })
    this.allCompanyIds = allCompanyIdsArr.join(",");
    this.setLocation("all");

    this.setCompany(this.allCompanyIds);
    this.locationService.getAllLocations().then(response => {
      console.log(response);
      this.companyLocations = response;      
    })
    .catch(err=>{this.handleError(err);});	
    this.setLoadUserTypeProfile();
  }

}



