import { Injectable } from '@angular/core';
import { RasPiDetails } from '../rasPiDetails';
import { ComData } from '../comData';
import { Headers, Http,RequestOptions } from '@angular/http';
import { HttpHeaders,HttpClient } from '@angular/common/http';
import { LoginBean } from '../typeBean/loginBean';
import { LoginService } from '../service/loginService';
import { environment } from '../../environments/environment';
import {BaseService} from './BaseService';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class ActivityService extends BaseService{
	constructor(private http: HttpClient, private loginService: LoginService) {
		super();
		this.appUrl = environment.appUrl?environment.appUrl:"";
	}
	appUrl = "";

	loginBean :LoginBean = new LoginBean();
	
	/* private handleError(error: any): Promise<any> {
	  console.error('An error occurred', error); // for demo purposes only
	  return Promise.reject(error.message || error);
	} */

	getActivityData(activitySeverity: string, lawArea: string):Promise<any>{
		let headers = this.loginService.getHeaders();
		let companyId = this.loginService.getCompany();
		let url = this.appUrl+`/rest/activity/`+ companyId+"?activitySeverity="+activitySeverity;
		if(lawArea){
			url = this.appUrl+`/rest/activity/`+ companyId+"/bylawandstatus/?activitySeverity="+activitySeverity +"&lawArea="+lawArea;
		}
		return this.http.get(url,headers).toPromise().then(response => response).catch(this.handleError);
	}
	
	setActivityStatus(companyId:string,activityId:string,remarks:string,compliedType: string){
		let headers = this.loginService.getHeaders();
		//let companyId = this.loginService.getCompany();
		let url = this.appUrl+`/rest/activity/changeStatus/`+ companyId + "/" + activityId + "?"+compliedType+"=true";
		let statusRemarks = {};
		if(remarks){
			statusRemarks['remarks'] = remarks;
		}
		return this.http.post(url,statusRemarks,headers).toPromise().then(response => response).catch(this.handleError);
	}
	
	downloadFile(fileId:string){
		const url = this.appUrl+'/rest/activity/document/'+fileId;
		window.open(url);
	}

	uploadFile(files,companyId,activityId,callback){
		//companyId = this.loginService.getCompany();
		const httpOptions = {
			headers: new HttpHeaders({
			  'Accept-Encoding':  'multipart/form-data'
			})
		  };
		
		const url = this.appUrl+`/rest/file/upload/`+companyId+"/"+activityId
		let formData = new FormData();
		for(let file of files){
			formData.append("uploadedFile",file);
		}
		return this.http.post(url,formData,httpOptions).toPromise().then((response)=>{callback(response);}).catch(this.handleError);
	}
}


