import { Injectable } from '@angular/core';
import { RasPiDetails } from '../rasPiDetails';
import { ComData } from '../comData';
import { Headers, Http,RequestOptions } from '@angular/http';
import { HttpHeaders,HttpClient } from '@angular/common/http';
import { LoginBean } from '../typeBean/loginBean';
import { LoginService } from '../service/loginService';
import { environment } from '../../environments/environment';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class ReportService {
	constructor(private http: HttpClient, private loginService: LoginService) {
		this.appUrl = environment.appUrl?environment.appUrl:"";
	}
	appUrl = "";
	loginBean :LoginBean = new LoginBean();
	
	private handleError(error: any): Promise<any> {
	  console.error('An error occurred', error); // for demo purposes only
	  return Promise.reject(error.message || error);
	}


	getReportData(frequency:any , reportType: string ):Promise<any>{
		console.log(frequency);
		let headers = this.loginService.getHeaders();
		let companyId = this.loginService.getCompany();
		const url = this.appUrl+`/rest/report?`+reportType+'='+frequency+'&companyId='+ companyId ;
		return this.http.get(url,headers).toPromise().then(response => response).catch(this.handleError);
	}

	getReportDowloadUrl(frequency:any , reportType: string ):string{
		console.log(frequency);
		let headers = this.loginService.getHeaders();
		let companyId = this.loginService.getCompany();
		const url = this.appUrl+`/rest/report/generateReport?`+reportType+'='+frequency+'&companyId='+ companyId ;
		return url;
	}
	
	
}


