import { Component,OnInit,ViewChild } from '@angular/core';
import { LoginService } from '../service/loginService';
import {FormGroup,FormControl,Validators} from '@angular/forms';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { trigger, transition, animate, style } from '@angular/animations'

@Component({
  selector: 'Register-Page',
  templateUrl: './app.register.html',
  styleUrls: ['./register.css'],
   animations: [
    trigger('slideInOut', [
      transition(':enter', [
        style({transform: 'translateY(-100%)'}),
        animate('200ms ease-in', style({transform: 'translateY(0%)'}))
      ]),
      transition(':leave', [
        animate('200ms ease-in', style({transform: 'translateY(-100%)'}))
      ])
    ])
  ]
})
export class RegisterComponent implements OnInit{
	form;
	constructor(private loginService: LoginService,private modalService: NgbModal) { }

	responseStr:any = {};
	hasError = false;
	searchText = '';
	showSuccessMessage = false;
	locationLoading = true;
	isPresent = false;
	locations = [];
	
	private handleError(error: any): Promise<any> {
	  console.error('An error occurred', error); // for demo purposes only
	  return Promise.reject(error.message || error);
	}
	
	setFilter(selectedCity){
		console.log(selectedCity);
		this.searchText = selectedCity;
	}
	
	getLocation():void{
		var locationArr = [];
		this.loginService.getAllLocations().then(locationsResps=>{
			locationsResps.forEach(locationsResp=>{
				var loc = {text:"",id:""};	
				loc.text = locationsResp.locationName;
				loc.id = locationsResp.locationId;
				locationArr.push(loc);	
			});
		this.locations = locationArr;
		this.locationLoading = false;
		});
	}
	
	register(user:any): void {
	  this.loginService.register(user).
	  then(responseStr => {
		console.log(responseStr);
		this.showSuccessMessage = true;
		this.responseStr = responseStr;})
			.catch(err=>{this.handleError(err);this.hasError = true;});
			
	}
	
	
	hideError():void{
		this.hasError = false;
	}
	
	
	ngOnInit(): void {
		this.form = new FormGroup({ 
			firstName:new FormControl("",Validators.required),
			lastName:new FormControl("",Validators.required),
			userId:new FormControl("",Validators.required),
			pass:new FormControl("",Validators.required),
			retypePassword:new FormControl("",Validators.required),
			email:new FormControl("",Validators.required),
			phone:new FormControl("",Validators.required),
			companyName:new FormControl("",Validators.required),
			city:new FormControl("",Validators.required),
			state:new FormControl("",Validators.required),
			location:new FormControl("",Validators.required),
		});
		this.getLocation();			
	}
	changeHere(){
		console.log("Change...");
	}
	
	formObject({firstName,lastName,userId,pass,email,phone,companyName,city,state,location}){
		var object = {};
		object["userBean"] = {firstName,lastName,userId,pass,email,phone};
		object["companyBean"] = {companyName,city,state,location};
		return object;
	}
	
	isUserExist(userId){
		this.loginService.isUserExist(userId).then(isPresent=>{this.isPresent=isPresent?true:false});
	}
	
	onSubmit(user){
		delete user.retypePassword;
		delete user.address;
		if(this.form.valid){
			let registerd = this.register(this.formObject(user));
		}
		
	}
	  
}



