import { AfterViewInit, Component, OnInit, Input , ViewChild, ElementRef } from '@angular/core';
import { ActivityService } from '../service/activityService';
import { RepositoryService } from '../service/repositoryService';
import { DataTableDirective } from 'angular-datatables';
import {LoginService} from '../service/loginService';
import { NgbModalModule,NgbModal,  ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import * as $ from 'jquery';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-compliance-activities',
  templateUrl: './compliance-activities.component.html',
  styleUrls: ['./compliance-activities.component.css'],
  providers: [ActivityService,RepositoryService]
})
export class ComplianceActivitiesComponent implements OnInit, AfterViewInit  {

  constructor(private activityService: ActivityService,private repositoryService: RepositoryService, private modalService: NgbModal,private loginService:LoginService) { }

 // public activityData:Array<Object> = [];
  public loaded:boolean = false;


  @ViewChild(DataTableDirective)
  datatableElement: DataTableDirective;

  dtOptions: any = {};
  dtTrigger: Subject<any> = new Subject();
  public popupDetails:any = {};
  public filesDetails:any = undefined;
  private closeResult: string;
  public addedFiles;
  public filesToUpload = [];
  private remarks:string;
  private latestActivityId:any; 
  private latestCompanyId:any; 
  private captureEvent:any;
  private filesUploading:boolean = false;
  public pendingCompliance:boolean = true;
  public pendingForReview:boolean = true;
  public showerror:boolean = false;
  private complied_type:string;

  private modalReference:any;

  @Input() activityData: any;
  userType;

  @ViewChild('popupcontent') popupcontent: ElementRef;
  @ViewChild('confirmNotApplicable') confirmNotApplicable: ElementRef;

  changeStatus(activity, activityId,companyId,event){
    console.log(activity);
    this.latestActivityId = activityId;
    this.remarks = undefined;
    this.latestCompanyId = companyId;
    this.captureEvent = event;
    this.repositoryService.getRepositoryFilesData(companyId,activityId).then(response => {
      this.loadFileDetails(response);
      console.log(this.filesDetails);
      if(this.filesDetails && this.filesDetails.length==0){

          this.modalReference = this.modalService.open(this.confirmNotApplicable, { size: 'lg', backdrop: 'static' });
          this.modalReference.result.then((result) => {
            this.closeResult = `Closed with: ${result}`;
          }, (reason) => {
            this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
          });
      }else {
        console.log(activityId,companyId,event);
        
        this.submitStatusChange(companyId,activityId,this.remarks,event,'complied');
        // this.activityService.setActivityStatus(activityId).then(resp=>{
        //   event.target.disabled = true;
        //   event.target.value = 'Complied';
        // });
      }
    }).catch(err=>{this.handleError(err);});

  }

  submitStatusChange(companyId, activityId,remarks,event,compliedType) {
this.activityService.setActivityStatus(companyId, activityId,remarks,compliedType).then(resp=>{
  console.log(event);
        event.target.disabled = true;
        $(event.target).parent().html(compliedType);
        //event.target.value = compliedType;
      });
  }

  NotApplicaleRemark(): void {

      if(this.remarks && this.remarks != ""){
        this.submitStatusChange(this.latestCompanyId,this.latestActivityId,this.remarks,this.captureEvent,"complied");
        this.modalReference.close();
      }else {
        this.showerror = true;
      }
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  public setLoadUserTypeProfile(){
    this.userType = this.loginService.getUserRole();
  }

/*
  downloadFile(activityId:string,companyId:string,fileId:string){
    console.log(activityId,companyId,fileId);
    this.activityService.downloadFile(fileId);
  }
*/
   private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  private openModal(data:any) {
    //Clear data
    this.filesToUpload = [];

    this.filesDetails = undefined;
    
    this.repositoryService.getRepositoryFilesData(data.companyId, data.activityId).then(response => {this.loadFileDetails(response);})
      .catch(err=>{this.handleError(err);});
    
  	this.popupDetails = data;
  	this.modalService.open(this.popupcontent, { size: 'lg', backdrop: 'static' }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  loadFileDetails(response){
    console.log("Files response",response);
    this.filesDetails = response;
  }

  private downloadFile(fileId:string){
    this.repositoryService.downloadFile(fileId);
  }

  public setFiles(event){
    //console.log("Hoiiiiioioio...",event.target.files);
    for(let files of event.target.files){
      this.filesToUpload.push(files);
    }
  }

  uploadComplete(companyId,activityId,resp){
    console.log("Upload complete",resp);
    this.filesToUpload = [];
    this.filesUploading = false;
    this.repositoryService.getRepositoryFilesData(companyId,activityId).then(response => {this.loadFileDetails(response);})
      .catch(err=>{this.handleError(err);});
  }

  uploadSelectedFiles(activityId,companyId){
    console.log(companyId,activityId);
    let self = this;
    this.filesUploading = true;
    this.activityService.uploadFile(this.filesToUpload,companyId,activityId,(resp)=>{self.uploadComplete(companyId,activityId,resp)}).catch(err=>{this.filesUploading = false;this.handleError(err);});
  }

  removeFileFromList(indexToRemove){
    this.filesToUpload.splice(indexToRemove,1);
  }

  rerender(): void {
     let self = this;
    if(this.datatableElement && this.datatableElement.dtInstance){
      this.datatableElement.dtInstance.then((dtInstance: DataTables.Api) => {
        // Destroy the table first
        dtInstance.destroy();
        // Call the dtTrigger to rerender again
        self.datatableElement.dtTrigger.next();
      }); 
    }else{
      if(this.datatableElement){
        this.datatableElement.dtTrigger.next(); 
      }
      
    }
    
   }

  buildActivities(response):any{
        //  this.loaded = true;
          let self = this;
          let issManagerDirectClick = false;
this.dtOptions = {
      data: response,
      autoWidth:false,
      columns: [{
        title: 'Abbr',
        data: 'abbriviation',
        width:'2%'
      }, {
        title: 'Location',
        data: 'locationName',
        width:'15%'
      }, {
        title: 'Law Description',
        data: 'lawDesc',
        width:'20%'
      },{
        title: 'Activity Name',
        data: 'activityName',
        width:'30%'
      }, {
        title: 'Periodicity Due Date',
        data: 'duedate',
        width:'10%'
      }, {
        title: 'Period Description',
        data: 'periodicityDesc',
        width:'10%'
      },{
        title: 'Actvity Assign User',
        data: 'assignedUser',
        width:'10%'
      },{
        title: '',
        bSortable:false,
        render: function (data: any, type: any, full: any) {
          if(full.isComplied && !(full.isComplianceApproved || full.isComplianceRejected)){
            if(self.userType == 'ArTechUser') {
                  return "<select  class='btn btn-outline-primary' style='width: 100%;' (change)='onCompanyChange($event.target.value)'>"+
                          "<option>Select</option>" +
                          "<option value='notDue'>Not Due</option>" +
                          "<option value='compliedInTime'>Complied-in-time</option>" +
                          "<option value='compliedDelayed'>Complied-Delayed</option>" +
                          "<option value='pendingCompliance'>Pending Compliance</option>" +
                          "<option value='pendingDescrepancy'>Pending for Discrepancy</option>" +
                          "</select>";     
               }
            return "Pending for review";
          }else if(full.isComplied && full.isComplianceApproved && full.isComplainceDelayed){
            return "Complaince delayed";
          }else if(full.isComplied && full.isComplianceApproved && !full.isComplainceDelayed){
            return "Complaied in time";
          }else if(full.isComplied && full.isComplianceApproved && !full.isComplainceDelayed){
            return "Complied";
          }else if(!full.isComplied){
               if(self.userType == 'sManager' || self.userType == 'ArTechUser') {
                  return "To be complied";     
               }
               // else if (self.userType == 'ArTechUser'){
               //   return "<select  class='btn btn-outline-primary' style='width: 100%;' (change)='onCompanyChange($event.target.value)'>"+
               //            "<option>Select</option>" +
               //            "<option value='Not Due'>Not Due</option>" +
               //            "<option value='Complied-in-time'>Complied-in-time</option>" +
               //            "<option value='Complied-Delayed'>Complied-Delayed</option>" +
               //            "<option value='Pending Compliance'>Pending Compliance</option>" +
               //            "<option value='Pending for Discrepancy'>Pending for Discrepancy</option>" +
               //            "</select>";    
               // }

            return "<i id='statusChangeBtn' style='cursor:pointer' data-elemnt-obj=full class='fa fa-check fa-lg' value='Comply' title='Change status to complied'></i>";
          }
        }
      },{
        title: '',
        bSortable:false,
        render: function (data: any, type: any, full: any) {
            if(self.userType!='sManager'){
              return "<i *ngIf='full.fileId' style='cursor:pointer' class='fa fa-file-archive-o fa-lg downloadFile'></i>";  
            }
            return "";
            
        }
      }],
      rowCallback: (row: Node, data: any | Object, index: number) => {
        const self = this;
        // Unbind first in order to avoid any duplicate handler
        // (see https://github.com/l-lin/angular-datatables/issues/87)
        $('td:eq(7)', row).unbind('click');
        $('td:eq(7)', row).unbind('change');
        if(self.userType != 'ArTechUser' && self.userType != 'sManager'){
          $('td:eq(7)', row).bind('click','#statusChangeBtn', ($event) => {

            if(data.fileId){
              //self.downloadFile(data.activityId,data.companyId,data.fileId);
            }else {
                self.changeStatus(data,data.activityId,data.companyId,$event)
            }
          });
      }
        if(self.userType == 'ArTechUser'){
         $('td:eq(7)', row).bind('change','#arStatusChange', ($event) => {
           console.log(data);
           console.log($event.target['value']);
          // self.changeStatus(data,data.activityId,data.companyId,$event)
          self.submitStatusChange(data.companyId,data.activityId,null,$event,$event.target['value']);
          
        });
      }

        $('td:eq(8)', row).unbind('click');
        $('td:eq(8)', row).bind('click', () => {
          self.openModal(data);
        });
        return row;
      },
      columnDefs: [
                 { "visible": self.userType != 'sManager' ? true : false, "targets":  [8] }
               ],

// Declare the use of the extension in the dom parameter
      dom: 'Bfrtip',
      // Configure the buttons
      buttons: [
        'copy',
        'print',
        'excel'
      ]
    };
    this.loaded = true;
  }  

  onArStatusChange(event){
    console.log(event);
  }

private onFilterChange(): void {
    if(this.pendingCompliance && this.pendingForReview || !this.pendingCompliance && !this.pendingForReview){
      this.complied_type = undefined;
    }else if(this.pendingCompliance){
      this.complied_type = "Pending compliance";
    }else if(this.pendingForReview){
      this.complied_type = "Pending for review";
    }
    this.getArTechActivities();
  }

  getArTechActivities() {
    //this.loaded = false;
    let self = this;
    //self.loaded = false;
    this.activityService.getActivityData(this.complied_type,null).then(response => {
      self.loaded = false;
      this.buildActivities(response);
     // self.rerender();      
    }).catch(err=>{this.handleError(err);});
   
  }

  ngOnInit() {
      this.setLoadUserTypeProfile();
      this.buildActivities(this.activityData);
   
  }

  
  ngAfterViewInit(): void {
    this.dtTrigger.next();
    let that = this;
    $('input[columnLocation]', ).on('input', function () {
      let input = this;
      let index = parseInt($(input).attr("columnLocation"));
      that.datatableElement.dtInstance.then((dtInstance: DataTables.Api) => {
        dtInstance.columns(index).search(input['value']).draw();
      }); 
    });
   
  }
}



