import { Component, OnInit } from '@angular/core';
import { DashboardService } from '../service/dashboardService';
import {LoginService} from '../service/loginService';
import { LoginBean } from '../typeBean/loginBean';
import { Router } from '@angular/router';

@Component({
  selector: 'app-compliance-dashboard',
  templateUrl: './compliance-dashboard.component.html',
  styleUrls: ['./compliance-dashboard.component.css'],
  providers: [DashboardService]
})

export class ComplianceDashboardComponent implements OnInit {
  constructor(private dashboardService: DashboardService, private router: Router, private loginService:LoginService) { }

  loginBean :LoginBean = new LoginBean();

  private months:Array<string> = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
  public riskCount:Object = {};
  public complainceOverviewByLaw:Object = {};
  public pieChartData:number[] = [0, 0, 0, 0];
  public lawIds = {"Corporate law":"corporateLaw","Direct tax":"directTax","Indirect tax":"indirectTax","Labour law":"labourLaw"}
  public loaded = false;
  userType;
	
  public lineChartData:Array<any> = [
	{data:[0, 0, 0],label:'Pending compliance'},
    {data:[0, 0, 0],label:'Pending for review'},
    {data:[0, 0, 0],label:'Complied'}
  ];
  public lineChartLabels:Array<string> = [];
  
  objectKeys(obj) {
    return Object.keys(obj);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  buildDashBoard(response):any{
      this.riskCount = response.riskCount;
      this.complainceOverviewByLaw = response.complainceOverviewByLaw;
      this.pieChartData = [response.compliceOverview.pendingCompliance, response.compliceOverview.pendingReview ,response.compliceOverview.delayedCompliance ,response.compliceOverview.intimeCompliance ];
	  
	  var pendingCompli = [];
	  var pendingForReview = [];
	  var complied = [];
	  var that = this;
	  Object.keys(response.compliceOverviewLast3Month).forEach(function(key){
			console.log(response.compliceOverviewLast3Month[key]);
			complied.push(response.compliceOverviewLast3Month[key].intimeCompliance+response.compliceOverviewLast3Month[key].delayedCompliance);
			pendingForReview.push(response.compliceOverviewLast3Month[key].pendingReview);
			pendingCompli.push(response.compliceOverviewLast3Month[key].pendingCompliance);
			that.lineChartLabels.push(that.months[parseInt(key)-1]);
		});
		this.lineChartData[0].data = pendingCompli;
		this.lineChartData[1].data = pendingForReview;
		this.lineChartData[2].data = complied;
		console.log(this.lineChartLabels,this.lineChartData);
		this.loaded = true;
  }


// lineChart
  
  public lineChartType:string = 'line';
  public pieChartType:string = 'pie';
  public lineChartOptions:any = {
    responsive: true,
    title: {
        display: true,
        text: 'Monthly overview'
      }
  };

  public pieChartOptions:any = {
    responsive: true,
    title: {
        display: true,
        text: 'Compliance overview'
      }
  };
  
 
  // Pie
  public pieChartLabels:string[] = ['Pending compliance', 'Pending for review', 'Complied-Delayed', 'Complied- In time'];
  //public pieChartData:number[] = [300, 500, 100, 200];
 
  public randomizeType():void {
    this.lineChartType = this.lineChartType === 'line' ? 'bar' : 'line';
    this.pieChartType = this.pieChartType === 'doughnut' ? 'pie' : 'doughnut';
  }
 
  public chartClicked(e:any, chartType: string):void {
    if (e.active.length > 0) {
      const chart = e.active[0]._chart;
      const activePoints = chart.getElementAtEvent(e.event);
      if ( activePoints.length > 0) {
        // get the internal index of slice in pie chart
        const clickedElementIndex = activePoints[0]._index;
        let datasetIndex = activePoints[0]._datasetIndex
        const legend_label = chart.data.labels[clickedElementIndex];
        const line_label = chart.data.datasets[datasetIndex].label;
console.log(chartType);
        if(chartType == 'line'){
          this.getActivities(line_label, legend_label , null );  
        }else {
          this.getActivities(legend_label, undefined, null);
        }
        
      }
   }
  }
 
  public chartHovered(e:any):void {
    console.log(e);

  }

  public setLoadUserTypeProfile(){
    this.userType = this.loginService.getUserRole();
  }


  ngOnInit() {
       this.setLoadUserTypeProfile();
    this.dashboardService.getDashboardData().then(response => {this.buildDashBoard(response);})
      .catch(err=>{this.handleError(err);});
  }

  getActivities(activity, lawarea, event): void {
      if(event){
          event.preventDefault();
      }   
      let params:any = {};
      params.activitySeverity = activity;
      if(lawarea){
        params.lawArea = this.lawIds[lawarea]?this.lawIds[lawarea]:lawarea;
      }

    this.router.navigate(['/activities'],{ queryParams: params });
    // this.dashboardService.getActivities(data[0].companyId,activity).then(response => {console.log(response);})
    //   .catch(err=>{this.handleError(err);});
  }

}



