import { Component,Input,forwardRef, HostBinding,ViewChild } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

export const COMBO_BOX_VALUE_ACCESSOR : any = {
	provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => ComboboxComponent),
    multi: true
}

@Component({
  selector: 'ComboBox',
  templateUrl: './app.combobox.html',
  styleUrls: ['../../register/register.css'],
  providers: [COMBO_BOX_VALUE_ACCESSOR]
})

export class ComboboxComponent implements ControlValueAccessor{
	@Input() options :any;
	@Input() disabled = false;
	
	constructor() { 
		
	}

	onChange;
	
	onTouched = () => {};
	
	searchText : string = "";
	searchId : string = "";
	
	setFilter(selectedCity,searchId){
		this.searchText = selectedCity;		
		this.searchId = searchId;
		this.onChange(this.searchId);
	}
	
	writeValue(value:string):void{
		
	}
	  
	registerOnChange(fn: any): void {
		this.onChange = fn;
	}
	
	registerOnTouched(fn: any): void {
		this.onTouched = fn;
	}
	
	setDisabledState(isDisabled: boolean): void {
		this.disabled = isDisabled;
	}
	  
}



