import { Component, OnInit } from '@angular/core';
import { ReportService } from '../service/reportService';
import * as $ from 'jquery';

@Component({
  selector: 'app-compliance-report',
  templateUrl: './compliance-report.component.html',
  styleUrls: ['./compliance-report.component.css'],
  providers: [ReportService]
})
export class ComplianceReportComponent implements OnInit {

  constructor(private reportService: ReportService) { }

public view :String = "month";
public loaded:boolean = false;
//public reportCompany :number;

private monthSet = ['January','February','March','April','May','June','July ','August','September','October','November','December' ];
private yearSet = ['2015-16', '2016-17', '2017-18','2018-19'];
private quarterSet = ['April - June', 'July - September', 'October - December','January - March'];
public reportYear :string = this.yearSet[0];
private date:any = new Date();
public reportMonth :any = this.date.getMonth();
private companies:string = sessionStorage.getItem('companies');
private companySet:Array<Object> = JSON.parse(this.companies);
//public reportCompany :string = this.companySet[0]['companyId'];
private activities:Array<Object> = [];
private defaultActivityState:string = 'open';
private quarterPosition:number = Math.floor((this.date.getMonth() + 3) / 3) -1 -1;
public reportQuarter :number = this.quarterPosition == -1 ? 3 : this.quarterPosition;
private startingReportNote = '';
private endingReportNote = '';


private getReportNote() {
  if(this.view == "month"){
    this.startingReportNote = "for the month of "+this.monthSet[this.reportMonth] + "(FY" + this.getCurrentFiscalYear()+")" ;
    this.endingReportNote = "(till "+this.monthSet[this.reportMonth] + " end)";
  }else if(this.view == "quarter") {
    this.startingReportNote = "for the quarter of "+this.quarterSet[this.reportQuarter] + "(FY" + this.getCurrentFiscalYear()+")" ;
    this.endingReportNote = "(till quarter "+this.quarterSet[this.reportQuarter] + " end)";
  }else if(this.view == "year") {
    this.startingReportNote = "for "+this.reportYear ; 
    this.endingReportNote = "(till 30th of April'"+this.date.getFullYear()+  ")";
  }
}



public barChartOptions:any = {
    scaleShowVerticalLines: false,
    responsive: true,
    title: {
        display: true,
        text: 'January (FY 2016-17)'
    },
	scales: {
		xAxes: [{
			display: true,
			scaleLabel: {
				display: true,
				labelString: 'Month'
			},
			ticks: {
				min: 0,
				stepSize: 1
			}
		}]
	}
  };
  public barChartLabels:string[] = ['High','Low', 'Medium'];
  public barChartType:string = 'horizontalBar';
  public barChartLegend:boolean = true;
 
  public barChartData:any[] = [
    {data: [65, 59, 80], label: 'Complied Activities'},
    {data: [28, 48, 40], label: 'Open Activities'}
  ];
 

	private selectAvtivities(activity:String): void {
	    if (activity == "open") {
	        $('.cloasedactivities, .delayedactivities').css('display', 'none');
        	$('.openactivities').fadeIn('slow');
	    }else if(activity == "closed"){
	        $('.openactivities, .delayedactivities').css('display', 'none');
        	$('.cloasedactivities').fadeIn('slow');
	    } else {
          $('.openactivities, .cloasedactivities').css('display', 'none');
          $('.delayedactivities').fadeIn('slow');
      }
	} 

    

  // events
  public chartClicked(e:any):void {
    console.log(e);
  }
 
  public chartHovered(e:any):void {
    console.log(e);
  }

private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }


  public riskTableData:Object = {};
  objectKeys(obj) {
    return Object.keys(obj);
  }

  private getCurrentFiscalYear(): string{
    let fiscalYr = "";
    if (this.date.getMonth() > 2) { //
        let nextYr1 = (this.date.getFullYear() + 1).toString();
        fiscalYr = this.date.getFullYear().toString() + "-" + nextYr1.charAt(2) + nextYr1.charAt(3);
    } else {
        let nextYr2 = this.date.getFullYear().toString();
        fiscalYr = (this.date.getFullYear() - 1).toString() + "-" + nextYr2.charAt(2) + nextYr2.charAt(3);
    }
     
    return fiscalYr;
 }

  buildReport(response):any{
	 
    let risktable = response.riskCount;//{Low: {complied: 1, open : 4 }, Medium: {complied: 5, open : 1 }, High: {complied: 3, open : 2 }}
    this.riskTableData = risktable;
    let compliedData = [];
    let openData = [];
    for(let i=0, risks = this.objectKeys(risktable) ; i < risks.length ; i++){
      compliedData.push(risktable[risks[i]]['complied']);    
      openData.push(risktable[risks[i]]['open']);    
    }
    
    this.barChartData[0].data = compliedData;
    this.barChartData[1].data = openData;
    this.barChartOptions.title.text = this.monthSet[this.reportMonth] + " (FY "+ this.getCurrentFiscalYear() + " )";
 
    let activities = response.activities;//[{"companyId":"comp","abbriviation":"com","lawId":"labourLaw","lawDesc":"Assam Goods & Service Tax","lawName":"Labour Law","locationId":"haryana","locationName":"Haryana","activityId":"4","activityName":"Monthly outward supplies return","riskId":"low","riskDes":"Low","periodicityId":"Feb18","periodicityDesc":"Feb'18","periodicityDateId":"2018-04-10","duedate":"2018-04-10","isComplied":true,"isComplianceApproved":null,"isComplianceRejected":null},{"companyId":"comp","abbriviation":"com","lawId":"AAA","lawDesc":"Income tax","lawName":"Direct Tax","locationId":"haryana","locationName":"Haryana","activityId":"1","activityName":"TDS on Salary Payment","riskId":"medium","riskDes":"Medium","periodicityId":"Feb18","periodicityDesc":"Feb'18","periodicityDateId":"2018-04-30","duedate":"2018-04-30","isComplied":null,"isComplianceApproved":null,"isComplianceRejected":null},{"companyId":"comp","abbriviation":"com","lawId":"indirectTaxHar","lawDesc":"Haryana Goods & Service Tax","lawName":"Indirect Tax","locationId":"haryana","locationName":"Haryana","activityId":"3","activityName":"Payment of TDS on overseas Remittance","riskId":"medium","riskDes":"Medium","periodicityId":"Mar18","periodicityDesc":"Mar'18","periodicityDateId":"2018-04-30","duedate":"2018-04-30","isComplied":null,"isComplianceApproved":null,"isComplianceRejected":null},{"companyId":"comp","abbriviation":"com","lawId":"labourLaw","lawDesc":"Assam Goods & Service Tax","lawName":"Labour Law","locationId":"haryana","locationName":"Haryana","activityId":"5","activityName":"Submission of monthly return (PART B)","riskId":"low","riskDes":"Low","periodicityId":"Mar18","periodicityDesc":"Mar'18","periodicityDateId":"2018-04-20","duedate":"2018-04-20","isComplied":null,"isComplianceApproved":null,"isComplianceRejected":null},{"companyId":"comp","abbriviation":"com","lawId":"AAA","lawDesc":"Income tax","lawName":"Direct Tax","locationId":"haryana","locationName":"Haryana","activityId":"2","activityName":"TDS on other resident payment","riskId":"medium","riskDes":"Medium","periodicityId":"Mar18","periodicityDesc":"Mar'18","periodicityDateId":"2018-04-30","duedate":"2018-04-30","isComplied":null,"isComplianceApproved":null,"isComplianceRejected":null}]
    this.activities = activities;
    this.loaded = true;
   // this.hideShowActivities();
  }

  private getReportData(): void {
	  this.loaded = false;
    //let companyId = this.reportCompany;
    let freqency = null;
    let view = null;
    if(this.view == 'month'){
      view = 'month';
      freqency = parseInt(this.reportMonth)+1;
    }else if(this.view == 'quarter'){
      view = 'quarter';
      freqency = this.reportQuarter;
    }else {
      view = 'year';
      freqency = this.reportYear;//this.yearSet[this.reportYear];
    }
    this.getReportNote();
    this.reportService.getReportData(freqency, view).then(response => {this.buildReport(response);})
      .catch(err=>{this.handleError(err);});   
  
  }

  private downloadReportData():void{
    let freqency = null;
    let view = null;
    if(this.view == 'month'){
      view = 'month';
      freqency = parseInt(this.reportMonth)+1;
    }else if(this.view == 'quarter'){
      view = 'quarter';
      freqency = this.reportQuarter;
    }else {
      view = 'year';
      freqency = this.reportYear;
    }
    let url = this.reportService.getReportDowloadUrl(freqency, view);
    window.open(url);
    //console.log(url);
  }
 
  hideShowActivities() {
    $('.cloasedactivities, .delayedactivities').css('display', 'none');
    $('openactivities').fadeIn('slow');
  }  

  ngOnInit() {
    this.getReportData();
    this.getReportNote();
  }

}



