import { Injectable } from '@angular/core';
import { RasPiDetails } from '../rasPiDetails';
import { SwitchDetails } from '../typeBean/switchDetails';
import { LoginBean } from '../typeBean/loginBean';
import { ComData } from '../comData';
import { Headers, Http,RequestOptions } from '@angular/http';
import { HttpHeaders,HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class LoginService {
	
	constructor(private http: HttpClient) {
		console.log({environment});
		this.appUrl = environment.appUrl?environment.appUrl:"";
	}
	
	appUrl = "";
	userRole = undefined;
	private handleError(error: any): Promise<any> {
	  console.error('An error occurred', error); // for demo purposes only
	  return Promise.reject(error.message || error);
	}
	
	getHeaders(){
		let locationId = sessionStorage.getItem("location");
		let httpOptions = {headers: new HttpHeaders({ 'Content-Type':  'application/json'})};
		if(locationId && locationId!=null){
			httpOptions = {
				headers: new HttpHeaders({
				  'Content-Type':  'application/json',
				  'auth': sessionStorage.getItem("auth"),
				  'location':locationId
				})
			  };	
			 
		}else if(sessionStorage.getItem("auth")!=undefined){
			httpOptions = {
				headers: new HttpHeaders({
				  'Content-Type':  'application/json',
				  'auth': sessionStorage.getItem("auth")
				})
			  };
			  
		}
		return httpOptions;
	}
	
	handleUserAction(response):void{
		sessionStorage.setItem("loggedIn","true");
		sessionStorage.setItem("auth",response.authKey);
		sessionStorage.setItem("companies",JSON.stringify(response.companies));
		sessionStorage.setItem("userType",response.userType);
	}
	
	isLoggedIn():string{
		return sessionStorage.getItem("loggedIn");
	}
	
	test():Promise<any>{
		const url = this.appUrl+`/rest/user/test`;
		return this.http.get(url).toPromise().then(response => response).catch(this.handleError);
	}
	
	loginUserPost(username:string,password:string){
		const url = this.appUrl+`/rest/user/login`;
		return this.http.post(url,{username,password}).toPromise().then(response => {response as LoginBean;
		this.handleUserAction(response);
		return response;
		}).catch(this.handleError);
	}
	
	register(registerBean){
		const url = this.appUrl+`/rest/user/register`;
		return this.http.post(url,registerBean).toPromise().then(response => {return response;/*this.handleUserAction()*/}).catch(this.handleError);
	}
	
	loginUser(username:string,password:string):Promise<LoginBean>{
		const url = this.appUrl+`/rest/user/login`;
		return this.http.post(url,{}).toPromise().then(response => {
			console.log(response);
			return response; 
		}).catch(this.handleError);
	}
	
	loggedInUser(){
		let headers = this.getHeaders();
		const url = this.appUrl+`/rest/user`;
		return this.http.get(url,headers).toPromise().then(response => {
			console.log(response);
			 return response;}).catch(this.handleError);
	}
	
	getAllLocations(){
		const url = this.appUrl+`/rest/location`;
		return this.http.get(url).toPromise().then(response => {console.log(response);return response;}).catch(this.handleError);
	}
	
	isUserExist(userName:string){
		const url = this.appUrl+`/rest/user/userAvailaible/`+userName;
		return this.http.get(url).toPromise().then(response => {console.log(response);return response;}).catch(this.handleError);
	}
	
	reSendUserActivationCode(){
		let headers = this.getHeaders();
		const url = this.appUrl+`/rest/user/resendActivationEmail`;
		return this.http.post(url,{},headers).toPromise().then(response => {console.log(response);return response;}).catch(this.handleError);
	}
	
	clearUserLogin(){
		sessionStorage.clear();
	}

	setLocation(location:string){
		sessionStorage.setItem("location",location);
	}

	getLocation(){
		return sessionStorage.getItem("location");
	}

	setCompany(company:string){
		sessionStorage.setItem("company",company);
	}

	getCompany(){
		return sessionStorage.getItem("company");
	}


	getUserRole(){
		return sessionStorage.getItem("userType");
	}
	
}


