import { AfterViewInit, Component, OnInit, Input , ViewChild, ElementRef } from '@angular/core';
import { RepositoryService } from '../service/repositoryService';
import { DataTableDirective } from 'angular-datatables';
import { ActivatedRoute } from '@angular/router';
import { ReportService } from '../service/reportService';
import { NgbModalModule,NgbModal,  ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import * as $ from 'jquery';


@Component({
  selector: 'app-compliance-repository',
  templateUrl: './compliance-repository.component.html',
  styleUrls: ['./compliance-repository.component.css'],
  providers: [RepositoryService,ReportService]
})
export class ComplianceRepositoryComponent implements OnInit,AfterViewInit {
//   @ViewChild(DataTableDirective)
//   datatableElement: DataTableDirective;
// dtOptions: DataTables.Settings = {};
// //dtOptions: DataTables.Settings = {};

  public repositoryData:any = [];
  public compliedInTimeData:any = [];
  public compliedDelayedData:any = [];
  public compliedInTime:boolean = true;
  public compliedDelayed:boolean = true;

  constructor(private repositoryService: RepositoryService, private route: ActivatedRoute, private modalService: NgbModal,private reportService: ReportService) { }
  @ViewChild(DataTableDirective)
  datatableElement: DataTableDirective;

  dtOptions: DataTables.Settings = {};



public view :String = "month";
public loaded:boolean = false;
//public reportCompany :number;

private monthSet = ['January','February','March','April','May','June','July ','August','September','October','November','December' ];
private yearSet = ['2015-16', '2016-17', '2017-18','2018-19'];
private quarterSet = ['April - June', 'July - September', 'October - December','January - March'];
public reportYear :string = this.yearSet[0];
private date:any = new Date();
public reportMonth :any = this.date.getMonth();
private quarterPosition:number = Math.floor((this.date.getMonth() + 3) / 3) -1 -1;
public repositoryQuarter :number = this.quarterPosition == -1 ? 3 : this.quarterPosition;
private sub: any = [];
private complied_type:string = "both";
private closeResult: string;
public popupDetails:any = {};
public filesDetails:any = undefined;


  @ViewChild('popupcontent') popupcontent: ElementRef;


  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

   private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  private openModal(data:any) {
    console.log(data);
    this.filesDetails = undefined;

    this.repositoryService.getRepositoryFilesData(data.companyId,data.activityId).then(response => {this.loadFileDetails(response);})
      .catch(err=>{this.handleError(err);});
    
  	this.popupDetails = data;
  	this.modalService.open(this.popupcontent, { size: 'lg', backdrop: 'static' }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

   private downloadFile(fileId:string){
   	
    this.repositoryService.downloadFile(fileId);
  }

  loadFileDetails(response){
    console.log("Files response",response);
    this.filesDetails = response;
  }

  buildRepository(activities):any{
     //this.repositoryData = response;
	this.dtOptions = {
      data: activities,
      columns: [{
        title: 'Abbreviation',
        data: 'abbriviation'
      }, {
        title: 'Location',
        data: 'locationName'
      }, {
        title: 'Law Description',
        data: 'lawDesc'
      },{
        title: 'Activity Name',
        data: 'activityName'
      }, {
        title: 'Periodicity Due Date',
        data: 'duedate'
      }, {
        title: 'Period Description',
        data: 'periodicityDesc'
      },{
        title: 'Date of compliance',
        data: 'completionDate'
      },{
        title: 'Actvity Assign User',
        data: 'assignedUser'
      },{
        title: '',
        render: function (data: any, type: any, full: any) {
            return "<i *ngIf='full.fileId' class='fa fa-download fa-lg downloadFile' style='cursor:pointer'></i>";
        }
      }],
      rowCallback: (row: Node, data: any[] | Object, index: number) => {
        const self = this;
        // Unbind first in order to avoid any duplicate handler
        // (see https://github.com/l-lin/angular-datatables/issues/87)
        $('td:eq(8)', row).unbind('click');
        $('td:eq(8)', row).bind('click','.downloadFile', () => {
          self.openModal(data);
        });
        return row;
      }

    };
    this.loaded = true;
  }

  getSelectedActivities(activitySeverity:string) {
	this.loaded = false;
	let that = this;

  	  let filteredActivities:any = [];
  //public compliedDelayedData:any = [];
  		if(activitySeverity === "Complied- In time") {
			filteredActivities = $.grep(this.repositoryData, function(activity, i){ // just use arr
			  return activity['isComplied'] && activity['isComplianceApproved'] && !activity['isComplainceDelayed'];
			});
  		}else {
			filteredActivities = $.grep(this.repositoryData, function(activity, i){ // just use arr
			  return activity['isComplied'] && activity['isComplianceApproved'] && activity['isComplainceDelayed'];
			});

  		}
  		setTimeout(function() {
  			
		that.buildRepository(filteredActivities);
	},2000)
  		

  }



  ngOnInit() {
// if(this.route.data) {
//     this.sub = this.route.data.subscribe(data => {
//     	console.log(data.repository_type);
//     	this.complied_type = data.repository_type;
//        // this.complied_type = params['activitySeverity'];
//        // console.log(this.complied_type);
//     });
//   }

 // this.complied_type = undefined;
  this.getReportData();
    // this.repositoryService.getRepositoryData(this.complied_type).then(response => {this.repositoryData = response;this.buildRepository(this.repositoryData);})
    //   .catch(err=>{this.handleError(err);});

  }

  private onFilterChange(): void {
  	if(this.compliedInTime && this.compliedDelayed){
  		this.complied_type = "both";
  	}else if(this.compliedInTime){
  		this.complied_type = "Complied- In time";
  	}else if(this.compliedDelayed){
  		this.complied_type = "Complied-Delayed";
  	}else{
      this.complied_type = "all"; 
    }
  	this.getReportData();
  }

  private getReportData(): void {
	  this.loaded = false;
    //let companyId = this.reportCompany;
    let freqency = null;
    let view = null;
    if(this.view == 'month'){
       view = 'month';
     freqency = parseInt(this.reportMonth)+1;
    }else if(this.view == 'quarter'){
      view = 'quarter';
      freqency = this.repositoryQuarter;
    }else {
      view = 'year';
      freqency = this.reportYear;//this.yearSet[this.reportYear];
    }
    this.repositoryService.getRepositoryDataFiltered(this.complied_type,freqency, view).then(response => {this.buildRepository(response);})
      .catch(err=>{this.handleError(err);});   
  
  }

 ngAfterViewInit(): void {
    // this.datatableElement.dtInstance.then((dtInstance: DataTables.Api) => {
    //   dtInstance.columns().every(function () {
    //     const that = this;
    //     $('input', this.footer()).on('keyup change', function () {
    //       if (that.search() !== this['value']) {
    //         that
    //           .search(this['value'])
    //           .draw();
    //       }
    //     });
    //   });
    // });
  }


}



